<template>
  <div class="gallery">
    <div id="container">
        <div class="photo-box" :id="item.id" v-for="(item,index) in photoArr" :class="{'center':item.id==centerId}" @click="clickHandler(item)" :style="{left: item.left+'px',
    top: item.top+'px', transform: 'rotate('+item.rotate+'deg)'
  }">
            <div class="overturn">
                <div class="front" :style="{transform:'rotateY('+item.frontRotateY+'deg)'}">
                  <img :src="item.src"><p>{{item.name}}</p>
                </div>

                <div class="back" :style="{transform:'rotateY('+item.backRotateY+'deg)'}">
                  <div class="des">
                    <p>{{item.description}}</p>
                  </div>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
// import a from ' static/photo.json'/
import Axios from 'axios'
// const Axios = new AXIOS();

export default {
  name: 'gallery',
  data () {
    return {
      photoArr:[
            {
                "id":0,
                "name":"81",
                "description":"2006_01_22,Kobe Bryant 81",
                "src":"./static/image/0.jpg"
            },
            {
                "id":1,
                "name":"Manba Forever",
                "description":"1996-2016,Mamba Never Out 8&24 RIP",
                "src":"./static/image/1.jpg"
            },
             {
                "id":2,
                "name":"To Be Better",
                "description":"What I’m doing right now, I’m chasing perfection.",
                "src":"./static/image/2.jpg"
            },
            {
                "id":3,
                "name":"Gigi",
                "description":"RIP for Gigi",
                "src":"./static/image/3.jpg"
            },
            {
                "id":4,
                "name":"Kobe Bryant",
                "description":"I‘m really tired,man. I just don't want to do this anymore.",
                "src":"./static/image/4.jpg"
            },
            {
                "id":5,
                "name":"8&24",
                "description":"Well, what the hell did you think I was?",
                "src":"./static/image/5.jpg"
            },
             {
                "id":6,
                "name":"Mamba out",
                "description":"Manba never out",
                "src":"./static/image/6.jpg"
            },
            {
                "id":7,
                "name":"Father",
                "description":"Love me or hate me, it's one or the other. Always has been. Hate my game, my swagger. Hate my fadeaway, my hunger. Hate that I'm a veteran. A champion. Hate that. Hate it with all your heart. And hate that I'm loved, for the exact same reasons.",
                "src":"./static/image/7.jpg"
            },
            {
                "id":8,
                "name":"Champion",
                "description":"sure someone wants to win, is that why can't i?",
                "src":"./static/image/8.jpg"
            },
             {
                "id":9,
                "name":"Good BYE",
                "description":"Good bye",
                "src":"./static/image/9.jpg"
            },
            {
                "id":10,
                "name":"RIP",
                "description":"Mamba",
                "src":"./static/image/10.jpg"
            },
            {
                "id":11,
                "name":"Mamba Forever",
                "description":"1996-2016,Mamba Never Out 8&24 RIP",
                "src":"./static/image/background1.gif"
            }
        ],
      centerId:0
    }
  },
  mounted(){
    // this.readJson()
    this.getPhotoArr(()=>{
          
          this.photoArr.forEach((item,index)=>{
            this.randomPos(item);
          });
        });
    
      

    
        
   },
  methods:{
    readJson(){
      var that=this
      Axios({
            method:'get' ,
            url: '/static/photo.json',
            dataType: "json",
            crossDomain: true,
            cache: false
            }).then(res => {
              // var arr1 = new Array();
              that.photoArr=res.data

             
              
        
          
            
         console.log(res.data)  
          })
      
    },


    getPhotoArr(cal){
       let params = {
        // api: './static/photo.json',
        param: ''
      };
      
            this.photoArr= this.photoArr
            // console.log(this.photoArr)
            cal();
       
    },
    clickHandler(item){
      // console.log(this.photoArr)
      var curId=item.id;
      if(curId==this.centerId){
          this.overturn(item);
      }
      else{
          this.centerId=curId;
          this.photoArr.forEach((item,index)=>{
            if(item.id!=this.centerId){
              this.randomPos(item);
            if(item.frontRotateY==180)
              this.overturn(item);
            }
          });
      }
    },
    overturn(item){
      if(item.frontRotateY){
        if(item.frontRotateY==180){
          item.frontRotateY=0;
          item.backRotateY=180;
        }else{
          item.frontRotateY=180;
          item.backRotateY=0;
        }
      }else{
          this.$set(item,"frontRotateY",180);
          this.$set(item,"backRotateY",0);  
      }
    },
    random(j,k){                       
      return Math.floor(Math.random()*k)+j;
    },
    randomPos(item){ 
      if( typeof item.left == 'undefined' ){
          //Vue.set(product,"checked",true);  全局注册
          this.$set(item,"left",this.random(-130,document.body.offsetWidth-130)); //局部注册
          this.$set(item,"top",this.random(-170,document.body.offsetHeight-170)); 
          this.$set(item,"rotate",this.random(-90,90));
        }else{ 
          item.left=this.random(-130,document.body.offsetWidth-130);
          item.top=this.random(-170,document.body.offsetHeight-170);
          item.rotate=this.random(-90,90);
        }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



